<template>
	<div class="content">
		<div class="header">
			<div class="head">
				<router-link to="/user" class="back"></router-link>
				{{ $t('lang.recharge_pay1') }}
				<!-- <div class="recorde" @click="clickRecorde">
					<img src="../../assets/images/jilu@2x.png" />
				</div> -->
			</div>
		</div>
		<div class="code">
			<img class="img" :src="imgCode" />
			<div class="link" @click="downImg">
				<img src="../../assets/images/baoc@2x.png" />
				{{ $t('lang.recharge_txt2') }}
			</div>
		</div>
		<div class="bottom">
			<div class="link" @click="save">{{ $t('lang.submit') }}</div>
		</div>
	</div>
</template>

<script>
import Fetch from '../../utils/fetch';

export default {
	name: 'index',
	components: {},
	data() {
		return {
			data: {},
			type:null,
			imgCode:'',
			invest_min_money:null
		};
	},
	created() {
		this.type = this.$route.query.type;
		this.$parent.footer(false);
	},
	mounted() {
		this.start();
	},
	methods: {
		start() {//https://www.cxfvqor.com/api/api/webconfig///user/invest/channel
			Fetch('/user/invest/channel')
			.then(res => {
				this.invest_min_money = Number(res.data.invest_min_money);
			});
			Fetch('/api/api/webconfig',{type:'pay'}).then(res => {
				if(this.type=='wx'){
					this.imgCode = res.data.wx_code;
				}else if(this.type == 'alipay'){
					this.imgCode = res.data.alipay_code;
				}
			});
			
		},
		downImg() {
			if (!window.plus) return;
			window.plus.gallery.save(
				this.data.share_image_url,
				function() {
					// alert('保存成功!');
				},
				function() {
				// alert('保存失败，请重试!');
				}
			);
		},
		save(){
			var that = this;
			var tmp = {
				money:this.$route.query.money,
				invest_min_money:this.invest_min_money,
				channel:this.type.toLowerCase(),
				voucher:' '
			}
			Fetch('/user/invest/usdt', tmp)
			.then(res => {
				if(res.code!=200){
					return;
				}
				this.$notify({
					background: '#EAF1FF',
					color: '#3292FF',
					message: this.$t('lang.submit_s'),
				});
				setTimeout(function(){
					that.$router.replace({
						path: '/user',
					});
				},3000)
			});
			return;
		}
	},
};
</script>

<style lang="scss">
.content{
	min-height: 100%;
	background: #F1F1F1;
}
.code{
	margin: 10px;background-color: #fff;padding:40px 0;border-radius: 5px;
	text-align: center;
	.img{width: 200px;height: 200px;margin-bottom: 35px;}
	.link{
		background: #00B389;
		border-radius: 5px;margin: 0 auto;
		width: 200px;
		height: 40px;line-height: 40px;vertical-align: middle;
		font-size: 14px;
		font-family: PingFang SC;
		font-weight: 500;
		color: #FFFFFF;
		img{width: 17px;height: 17px;}
		
	}
}
.bottom{
	position: absolute;bottom:0;left:0;
	background: #FFFFFF;width: 100%;padding:20px 0;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
	border-radius: 15px 15px 0px 0px;
	.link{
		width: 200px;text-align: center;
		height: 45px;line-height: 45px;
		background: linear-gradient(90deg, #2DBCCF, #14BA94);
		border-radius: 23px;
		font-size: 16px;
		font-family: PingFang SC;
		font-weight: 500;
		color: #FFFFFF;margin:0 auto;
	}
}
</style>