<template>
	<div class="content">
		<div class="header">
			<div class="head">
				<router-link to="/user" class="back"></router-link>
				{{ $t('lang.recharge_pay1') }}
				<!-- <div class="recorde" @click="clickRecorde">
					<img src="../../assets/images/jilu@2x.png" />
				</div> -->
			</div>
		</div>
		<div class="type" v-if="payMethod == 'USDT'">
			<div class="item" @click="clickType()">
				<img src="../../assets/images/usdt@2x.png" />
				<div class="txt">USDT [{{type}}]</div>
				<div class="link">{{ $t('lang.recharge_txt1') }}</div>
				<div class="arrow">
					<img src="../../assets/images/fanhui@2x.png" />
				</div>
			</div>
		</div>
		<div class="number" v-if="payMethod == 'USDT'">
			<div class="title">
				<img src="../../assets/images/fanh@2x.png" />
				{{ $t('lang.recharge_txt5') }}
			</div>
			<div class="Input">
				<input type="number" v-model="number" :placeholder="$t('lang.min_recharge') +':' +$t('lang.currency_icon') +data.invest_min_money" />
			</div>
			<div class="note">
				≈{{number*usdtRate}}
			</div>
		</div>
		<div class="payImg">
			<div class="title">{{ $t('lang.recharge_txt6') }}</div>
			<div class="img">
				<van-uploader
				  :before-read="beforeRead"
				  :after-read="afterRead"
				  :max-count="1"
				>
					<div class="add" icon="photo" type="primary">
						<div v-if="!payImg">
							<img src="../../assets/images/jiahao@2x.png" />
							<div>{{ $t('lang.recharge_txt7') }}</div>
						</div>
						<img class="Img" :src="payImg" alt="" v-if="payImg" />
					</div>
				</van-uploader>
				<!-- <img class="Img" :src='payImg' v-if="payImg" />
				<div class="add" v-if="!payImg">
					<div>
						<img src="../../assets/images/jiahao@2x.png" />
						<div>{{ $t('lang.recharge_txt7') }}</div>
						<input type="file" name="" id="" accept="image/jpeg,image/jpg,image/png" @change="changeImage" />
					</div>
				</div> -->
			</div>
		</div>
		<div class="bottom">
			<div class="link" @click="save">{{ $t('lang.submit') }}</div>
		</div>
	</div>
</template>

<script>
import Fetch from '../../utils/fetch';
import Vue from 'vue';
import { Uploader } from 'vant';
import axios from 'axios';
Vue.use(Uploader);
export default {
	data() {
		return {
			type:null,
			number:1000,
			payImg:null,
			data:{invest_min_money:null},
			usdtRate:null,
			payMethod:null,
		};
	},
	created() {
		this.type = this.$route.query.type;
		
	},
	mounted() {
		this.start();
		
	},
	methods: {
		start() {
			if(this.type=='wx' || this.type=='alipay'){
				this.payMethod = this.type;
				this.number = this.$route.query.money;
			}else if(this.type=='TRC20' || this.type == 'ERC20'){
				this.payMethod = 'USDT';
			}
			Fetch('/user/invest/channel')
			.then(res => {
				this.data = res.data;
				this.data.invest_min_money = Number(res.data.invest_min_money);
			});
			Fetch('api/api/webconfig', { type: 'pay' })
			.then(res => {
				this.usdtRate = res.data.rate;
			});
		},
		clickRecorde(){
			this.$router.replace({
				path: '/recharge_recorde',
			});
		},
		clickType(){
			this.$router.replace({
			  path: '/recharge_select',
			  query: {
				money: this.money,
			  },
			});
		},
		changeImage(event){
			//console.log(event);
			var file = event.target.files[0];
			var reader = new FileReader();
			var that = this;
			reader.readAsDataURL(file);
			//console.log(reader)
			reader.onload = function(e) {
				that.payImg = this.result;
			};
		},
		beforeRead(file) {
			if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
				this.$notify({
					message: this.$t('lang.type_limit'),
					color: '#FF3232',
					background: '#FFEAEA',
				});
				return false;
			}
			return true;
		},
		afterRead(file) {
			console.log(file);
			let formData = new FormData();
			formData.append('file', file.file);
			formData.append('ssid', localStorage.getItem('ssid'));
			axios.post('/api/api/upload', formData).then((r) => {
				console.log(r);
				if (r.data.code === 200) {
					Fetch('/user/info/upavatar', {
						avatar: r.data.data.file,
					}).then((res) => {
						this.$notify({
							background: '#EAF1FF',
							color: '#3292FF',
							message: this.$t('lang.edit_success'),
						});
						this.payImg = res.data.avatar;
					});
				} else {
					this.$notify({
						message: this.$t('lang.upload_fail'),
						color: '#FF3232',
						background: '#FFEAEA',
					});
				}
			});
		},
		save(){
			var that = this;
			
			if(!this.number || this.number<this.data.invest_min_money){
				this.$notify({
				  message: this.$t('lang.recharge_txt8'),
				  color: '#FF3232',
				  background: '#FFEAEA',
				});
				return;
			}
			if(!this.payImg){
				this.$notify({
				  message: this.$t('lang.recharge_txt9'),
				  color: '#FF3232',
				  background: '#FFEAEA',
				});
				return;
			}
			var tmp = {
				money:this.number,
				invest_min_money:this.data.invest_min_money,
				channel:this.type.toLowerCase(),
				voucher:this.payImg
			}
			Fetch('/user/invest/usdt', tmp)
			.then(res => {
				if(res.code!=200){
					return;
				}
				this.$notify({
					background: '#EAF1FF',
					color: '#3292FF',
					message: this.$t('lang.submit_s'),
				});
				setTimeout(function(){
					that.$router.replace({
						path: '/user',
					});
				},3000)
			});
			return;
		}
	},
};
</script>

