<template>
	<div class="content">
		<div class="header">
			<div class="head">
				<router-link to="/user" class="back"></router-link>
				{{ $t('lang.recharge_pay1') }}
				<!-- <div class="recorde" @click="clickRecorde">
					<img src="../../assets/images/jilu@2x.png" />
				</div> -->
			</div>
		</div>
		<div class="type" v-if="payMethod == 'USDT'">
			<div class="item" @click="clickType()">
				<img src="../../assets/images/usdt@2x.png" />
				<div class="txt">USDT [{{type}}]</div>
				<div class="link">{{ $t('lang.recharge_txt1') }}</div>
				<div class="arrow">
					<img src="../../assets/images/fanhui@2x.png" />
				</div>
			</div>
		</div>
		<div class="code" v-if="payMethod == 'USDT' || payMethod == 'wx' || payMethod == 'alipay'">
			<img class="img" :src="imgCode" />
			<div class="link" @click="downImg">
				<img src="../../assets/images/baoc@2x.png" />
				{{ $t('lang.recharge_txt2') }}
			</div>
		</div>
		<div class="urlLink" v-if="payMethod == 'USDT'">
			<div>{{ $t('lang.recharge_txt3') }}<span>USDT</span></div>
			<div class="item">
				<div class="txt">{{txt}}</div>
				<div id="copy_text" @click="copy" data-clipboard-action="copy" :data-clipboard-text="txt">
					<img src="../../assets/images/fuzhi@2x.png" />
				</div>
			</div>
		</div>
		<div class="bottom">
			<div class="link" @click="clickNext">{{ $t('lang.recharge_txt4') }}</div>
		</div>
	</div>
</template>

<script>
import Fetch from '../../utils/fetch';
export default {
	data() {
		return {
			data: {},
			type:null,
			txt:'',
			imgCode:'',
			payMethod:null,
		};
	},
	created() {
		this.type = this.$route.query.type;
		if(this.type=='wx' || this.type=='alipay'){
			this.payMethod = this.type;
		}else if(this.type=='TRC20' || this.type == 'ERC20'){
			this.payMethod = 'USDT';
		}
		Fetch('api/api/webconfig', { type: 'pay' })
		.then(res => {
			this.data = res.data;
			if(this.type=='TRC20'){
				this.txt=this.data.trc_address;
				this.imgCode = this.data.trc_code;
			}else if(this.type=='ERC20'){
				this.txt=this.data.erc_address;
				this.imgCode = this.data.erc_code;
			}else if(this.type=='wx'){
				this.imgCode = res.data.wx_code;
			}else if(this.type == 'alipay'){
				this.imgCode = res.data.alipay_code;
			}
		}); 
	},
	mounted() {
		
	},
	methods: {
		clickRecorde(){
			this.$router.replace({
				path: '/recharge_recorde',
			});
		},
		clickType(){
			this.$router.replace({
			  path: '/recharge_select',
			  query: {
				money: this.money,
			  },
			});
		},
		downImg() {
			if (!window.plus) return;
			window.plus.gallery.save(
				this.data.share_image_url,
				function() {
					// alert('保存成功!');
				},
				function() {
				// alert('保存失败，请重试!');
				}
			);
		},
		copy() {
			var clipboard = new this.Clipboard('#copy_text');
			clipboard.on('success', e => {
				this.$notify({
					message: this.$t('lang.copy_success'),
					color: '#3292FF',
					background: '#FFEAEA',
				});
				clipboard.destroy();
			});
			clipboard.on('error', e => {
				clipboard.destroy();
			});
		},
		clickNext(){
			this.$router.replace({
				path: '/recharge_pay2',
				query: {
					money: this.$route.query.money,
					type:this.$route.query.type
				},
			});
		}
	},
};
</script>

<style lang="scss">
