	.recorde{
		position: absolute;
		top: 50%;right:15px;
		-webkit-transform: translateY(-50%);
		-ms-transform: translateY(-50%);
		transform: translateY(-50%);
		img{width: 18px;}
	}
}
.content{
	min-height: 100%;
	background: #F1F1F1;
}
.type{
	padding: 10px;
	.item{
		display: flex;align-items: center;
		background-color: #fff;border-radius: 5px;padding:10px;
		font-size: 16px;
		font-family: PingFang SC;
		font-weight: bold;
		color: #333333;margin-bottom: 10px;
		img{width: 40px;height: 40px;margin-right: 10px;}
		.txt{flex:1;}
		.link{
			width: 80px;font-size: 14px;color: #fff;font-weight: 500;;
			height: 34px;line-height: 34px;
			background: linear-gradient(90deg, #2DBCCF, #14BA94);
			border-radius: 5px;text-align: center;
		}
		.arrow{
			img{width: 14px;height: 14px;margin-right: 0;margin-left:10px;}
		}
	}
}
.code{
	margin: 10px;background-color: #fff;padding:40px 0;border-radius: 5px;
	text-align: center;
	.img{width: 200px;height: 200px;margin-bottom: 35px;}
	.link{
		background: #00B389;
		border-radius: 5px;margin: 0 auto;
		width: 200px;
		height: 40px;line-height: 40px;vertical-align: middle;
		font-size: 14px;
		font-family: PingFang SC;
		font-weight: 500;
		color: #FFFFFF;
		img{width: 17px;height: 17px;}
		
	}
}
.urlLink{
	font-size: 16px;margin: 15px 10px 40px 10px;color: #333;
	span{color: #FF6600;}
	.item{
		background-color: #fff;border-radius: 5px;padding:10px;margin-top:10px;
		display: flex;align-items: center;
		.txt{color: #999999;line-height: 24px;flex:1;overflow: scroll;margin-right:10px;}
		img{width: 34px;}
	}
}
.bottom{
	position: absolute;bottom:0;left:0;
	background: #FFFFFF;width: 100%;padding:20px 0;
	box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
	border-radius: 15px 15px 0px 0px;
	.link{
		width: 200px;text-align: center;
		height: 45px;line-height: 45px;
		background: linear-gradient(90deg, #2DBCCF, #14BA94);
		border-radius: 23px;
		font-size: 16px;
		font-family: PingFang SC;
		font-weight: 500;
		color: #FFFFFF;margin:0 auto;
	}
}
</style>