<template>
	<div class="recharge_wrap">
		<div class="header">
			<div class="head">
				<router-link to="/user" class="back"></router-link>
				{{ $t('lang.recharge') }}
			</div>
		</div>
		<form class="form" method="post" @submit.prevent="handleSubmit">
			<div class="money_box" :lang="$i18n.locale">
				<div class="title1">{{ $t('lang.recharge_amount') }}</div>
				<div class="amount_box">
					<span>{{ $t('lang.currency_name') }}</span>
					<input
						type="number"
						step="0.01"
						name="money"
						v-model.number="money"
						:placeholder="
						$t('lang.min_recharge') +
						':' +
						$t('lang.currency_icon') +
						data.invest_min_money
						"
					/>
					<div class="clear_num" v-show="money > 0"  @click="clear_num_handle"></div>
				</div>
			</div>
			<div class="pay-type">
				<div>
					{{ $t('lang.access_money') }}
					：<span>{{ $t('lang.currency_icon') }}{{ data.money }}</span>
				</div>
			</div>
			<div class="fgline"></div>
			<div class="box box2">
				<div class="title2">{{ $t('lang.choose_recharge_type') }}</div>
				<div class="recharge_box" :lang="$i18n.locale">
					<div class="recharge_item clearfix" v-for="(channel, key) in data.config" :key="key">
						<input class="recharge_type" type="radio" :value="channel.key" v-model="type" />
						<span class="recharge_icon" :class="'recharge_' + channel.key"></span>
						<div class="recharge_icon_box">
							<p class="recharge_info">{{ channel.name }}</p>
							<div class="bank-remark" v-if="channel.key === 'bank' && channel.bank_is_back === 'Y' && channel.bank_back_apr > 0">
								{{ $t('lang.stroke_limit') }}
								{{ channel.bank_back_apr }}% {{ $t('lang.rebate_offer') }}
							</div>
						</div>
					</div>
					
					<!-- USDT  -->
					<div class="recharge_item clearfix" v-if="usdt_open=='Y'">
						<input class="recharge_type" type="radio" value="USDT" v-model="type" />
						<span class="recharge_icon recharge_bank"></span>
						<div class="recharge_icon_box">
							<p class="recharge_info">USDT</p>
						</div>
					</div>
					
				</div>
			</div>
			<div class="button-wrap">
				<button type="submit" :class="money > 0 ? '' : 'no_touch'" class="btn basic_btn_v2">
					{{ $t('lang.recharge_now') }}
				</button>
			</div>
		</form>
		<div id="mui-player"></div>
	</div>
</template>

<script>
import Fetch from '../../utils/fetch';
import MuiPlayer from 'mui-player';
import 'mui-player/dist/mui-player.min.css';
// 初始化 MuiPlayer 插件，MuiPlayer 方法传递一个对象，该对象包括所有插件的配置

export default {
	name: 'index',
	components: {},
	data() {
		return {
			data: {},
			type: '',
			money: '',
			usdt_open:'',
		};
	},
	created() {
		this.$parent.footer(false);
	},
	mounted() {
		// 初始化 MuiPlayer 插件，MuiPlayer 方法传递一个对象，该对象包括所有插件的配置
		var mp = new MuiPlayer({
			container: '#mui-player',
			title: '',
			src: '/media/kidkid888.R.mp4',
		});
		this.start();
	},
	methods: {
		start() {//https://www.cxfvqor.com/api/api/webconfig///user/invest/channel
			Fetch('/user/invest/channel').then(res => {
				this.data = res.data;
				this.data.invest_min_money = Number(res.data.invest_min_money);
			});
			Fetch('/api/api/webconfig',{type:'pay'}).then(res => {
				this.usdt_open = res.data.usdt_open;
			});
		},
		clear_num_handle() {
			this.money = '';
		},
		getClass(channel) {
			return 'recharge_online_' + channel;
		},
		handleSubmit() {
			if (!this.money) {
				this.$notify({
					message: this.$t('lang.input_money'),
					color: '#FF3232',
					background: '#FFEAEA',
				});
				return;
			}
			
			if (!this.type) {
				this.$notify({
					message: this.$t('lang.choose_recharge_type'),
					color: '#FF3232',
					background: '#FFEAEA',
				});
				return;
			}
			
			if (this.money < this.data.invest_min_money) {
				this.$notify({
					message:
						this.$t('lang.min_recharge') +
						' ' +
						this.$t('lang.currency_name') +
						this.data.invest_min_money,
					color: '#FF3232',
					background: '#FFEAEA',
				});
				return;
			}
	
			if (this.type == 'onepay') {
				Fetch('/api/api/onepaypayment', {
					money: this.money,
					invest_min_money: this.data.invest_min_money,
					payname: 'onepay_save',
				})
				.then(res => {
					//console.log(res);
					window.location.href = res.data.paymentUrl;
					return;
					// this.data = res.data;
					// this.data.invest_min_money = Number(res.data.invest_min_money);
				})
				.catch(function(error) {});
					//window.location.href = 'https://www.google.com';
				return;
			}
			
			if (this.type === 'bank') {
				this.$router.replace({
					path: '/recharge/bank',
					query: {
						money: this.money,
					},
				});
				return;
			}
			
			if (this.type === 'USDT') {
				this.$router.replace({
					path: 'recharge_select',
					query: {
						money: this.money,
					},
				});
				return;
			}
			
			if (this.type === 'wx'||this.type === 'alipay') {
				this.$router.replace({
					path: 'recharge_pay1',
					query: {
						type: this.type,
						money: this.money,
					},
				});
				return;
			}
			
			this.$router.replace({
				path: 'pay',
				query: {
					money: this.money,
					channel: this.type,
				},
			});
		},
	},
};
</script>

