<template>
  <div class="main">
    <div class="header">
      <div class="head">
        <router-link to="/user" class="back"></router-link>
        {{ $t('lang.register') }}
      </div>
    </div>
    <div class="top_bg" :lang="$i18n.locale">
      <h4>{{ $t('lang.hello') }}</h4>
      <p>{{ $t('lang.p_regist') }}</p>
    </div>
    <div class="newbg" :lang="$i18n.locale">
      <div class="choose_btn_wrap">
        <div class="choose_btn">
          <router-link to="/login" class="l_btn">{{ $t('lang.login') }}
          </router-link>
          <router-link to="/register" class="l_btn cur">{{ $t('lang.register') }}
          </router-link>
        </div>
      </div>

      <form class="form register" @submit.prevent="handleSubmit">
        <!-- <div class="content clearfix">
        <span class="left_icon phone_icon"></span>
        <input
          class="inp phone"
          id="phone"
          name="phone"
          type="text"
          v-model.trim="data.mobile"
          placeholder="请输入手机号码"
        />
        <span class="right right_icon clear_icon" @click="clear()"></span>
      </div> -->
        <div class="content clearfix">
          <p>{{ $t('lang.mobile') }}</p>
          <input class="inp phone" id="phone" name="phone" type="text" v-model.trim="data.mobile"
            :placeholder="$t('lang.input_phone')" />
          <!-- <span class="left_icon email_icon"></span>
          <input
            class="inp phone"
            id="email"
            name="email"
            type="text"
            v-model.trim="data.email"
            :placeholder="$t('lang.input_email')"
          />
          <span class="right right_icon clear_icon" @click="clear()"></span> -->
        </div>
        <div class="content clearfix hyzm">
          <div class="content1">
            <input class="inp inp1" id="smsCode" name="smscode" v-model.trim="data.code" type="text"
              :placeholder="$t('lang.input_varify_code')" />
            <van-count-down :time="time" class="right getsms" :lang="$i18n.locale" @finish="timeCall">
              <template v-slot="timeData">
                <span class="item" @click="sendcode">{{
                    timeData.seconds > 0 ? timeData.seconds : $t('lang.get_code')
                }}</span>
              </template>
            </van-count-down>
          </div>
        </div>
        <div class="content clearfix">
          <p>{{ $t('lang.password') }}</p>
          <div class="content1">
            <input class="inp pwd" :type="psd" name="pwd" v-model.trim="data.password"
              :placeholder="$t('lang.input_pw')" />
            <span class="right_icon show_icon" :class="psd == 'password' ? '' : 'show'" @click="show_psd"></span>
          </div>
          <!-- <span class="left_icon pwd_icon"></span>
          <input
            class="inp pwd"
            :type="psd"
            name="pwd"
            v-model.trim="data.password"
            :placeholder="$t('lang.set_login_pw')"
          />
          <span
            class="right_icon show_icon"
            :class="psd == 'password' ? '' : 'show'"
            @click="show_psd"
          ></span> -->
        </div>
        <div class="content clearfix">
          <p>{{ $t('lang.confirm_pw') }}</p>
          <div class="content1">
            <input class="inp pwd2" :type="psd" id="pwd2" name="pwd2" v-model.trim="data.spassword"
              :placeholder="$t('lang.set_login_pw_agein')" />
            <span class="right_icon show_icon" :class="psd == 'password' ? '' : 'show'" @click="show_psd"></span>
          </div>
          <!-- <span class="left_icon pwd_icon"></span>
          <input
            class="inp pwd2"
            :type="psd"
            id="pwd2"
            name="pwd2"
            v-model.trim="data.spassword"
            :placeholder="$t('lang.set_login_pw_agein')"
          />
          <span
            class="right_icon show_icon"
            :class="psd == 'password' ? '' : 'show'"
            @click="show_psd"
          ></span> -->
        </div>
        <!-- <div class="content clearfix tyzm">
          <div class="content1">
            <input
              class="inp inp1"
              type="text"
              v-model.trim="data.image_code"
              :placeholder="$t('lang.input_img_code')"
            />
            <img
              class="right click_code image_code"
              :src="base64Image"
              @click="getImageCode"
            />
          </div>
        </div> -->

        <!-- <div class="content clearfix">
        <input
          class="inp inp1"
          id="smsCode"
          name="smscode"
          v-model.trim="data.code"
          type="text"
          placeholder="请输入短信验证码"
        />
        <van-count-down :time="time" class="right getsms" @finish="timeCall">
          <template v-slot="timeData">
            <span class="item" @click="sendcode">{{
              timeData.seconds > 0 ? timeData.seconds : '获取验证码'
            }}</span>
          </template>
        </van-count-down>
      </div> -->
        <div class="content clearfix">
          <p>{{ $t('lang.to_name') }}</p>
          <input class="inp inp2" name="top" value="{$phone}" :readonly="is_t_inp" v-model.trim="data.t_mobile"
            type="text" placeholder="" />
          <input class="inp inp2" name="place_id" :readonly="is_t_inp" v-model.trim="data.place_id" type="hidden"
            placeholder="" />
          <!-- <span class="left_icon invite_icon"></span>
          <input
            class="inp inp2"
            name="top"
            value="{$phone}"
            :readonly="is_t_inp"
            v-model.trim="data.t_mobile"
            type="text"
            :placeholder="$t('lang.to_name')"
          />
          <input type="hidden" name="rec_phone" value="{$phone}" /> -->
        </div>

        <button type="submit" class="btn basic_btn_v2"
          :class="data.mobile != '' && data.password != '' ? '' : 'no_touch'">
          {{ $t('lang.register_rightnow') }}
        </button>

        <label class="contract">
          <van-checkbox v-model="checked" icon-size="14px">{{ $t('lang.has_agree') }}
          </van-checkbox>
          <!--                <router-link to="/regContact">用户协议</router-link>-->
          <router-link style="margin-left: 5px" v-if="config.user_contract_name" :to="config.user_contract_link">
            《{{ config.user_contract_name }}》
          </router-link>
          <router-link style="margin-left: 5px" v-if="config.user_contract_ys_name" :to="config.user_contract_ys_link">
            《{{ config.user_contract_ys_name }}》
          </router-link>
        </label>
      </form>
    </div>
    <router-link to="/login" class="login">{{ $t('lang.has_account') }}
    </router-link>
  </div>
</template>

<script>
import Vue from 'vue';
import qs from 'qs';
import { CountDown, Checkbox, Dialog } from 'vant';
import Fetch from '../../utils/fetch';

Vue.use(CountDown).use(Checkbox);

export default {
  name: 'index',
  data() {
    return {
      time: 0,
      bg: '',
      data: {
        t_mobile: '',
      },
      base64Image: '',
      is_t_inp: false,
      is_send: false,
      checked: true,
      psd: 'password',
      config: {},
    };
  },
  created() {
    this.$parent.footer(false);
  },
  mounted() {
    const params = location.search.replace('?', '').replace('/', '');
    var query = qs.parse(params);
    // var query = this.$route.query;
    this.data.t_mobile = query.m;
    this.data.place_id = query.qudao;
    console.log(query);
    if (query.m) {
      this.is_t_inp = true;
    }
    this.start();
    this.getImageCode();
  },
  methods: {
    show_psd() {
      console.log(this.psd);
      if (this.psd == 'password') {
        this.psd = 'text';
      } else {
        this.psd = 'password';
      }
    },
    getImageCode() {
      Fetch('/api/api/imageCode').then(res => {
        this.base64Image = res.data.code;
      });
    },
    start() {
      Fetch('/api/api/webconfig', { type: 'bg' }).then(res => {
        this.bg = 'url("' + res.data.reg + '")';
      });
      Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
        this.config = res.data;
      });
    },
    clear() {
      console.log(this.data.mobile);
      this.data.mobile = '';
    },
    timeCall() {
      this.is_send = false;
      this.time = 0;
    },
    sendemailcode() {
      if (this.is_send) {
        return;
      }

      if (!this.data.email) {
        this.$notify({
          message: this.$t('lang.input_email'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.image_code) {
        this.$notify({
          message: this.$t('lang.input_img_code'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      this.is_send = true;

      Fetch('/api/api/emailcode', {
        image_code: this.data.image_code,
        type: 'register',
        mobile: this.data.mobile,
        email: this.data.email,
      })
        .then(() => {
          this.time = 60 * 1000;
          this.$notify({
            background: '#EAF1FF',
            color: '#3292FF',
            message: this.$t('lang.send_success'),
          });
          this.getImageCode();
        })
        .catch(() => {
          this.is_send = false;
          this.getImageCode();
        });
    },
    sendcode() {
      if (this.is_send) {
        return;
      }
      console.log(this.data.mobile);
      if (!this.data.mobile) {
        this.$notify({
          message: this.$t('lang.input_phone'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      // if (!this.data.image_code) {
      //   this.$notify({
      //     message: this.$t('lang.input_img_code'),
      //     color: '#FF3232',
      //     background: '#FFEAEA',
      //   });
      //   return;
      // }

      this.is_send = true;

      Fetch('/api/api/code', {
        image_code: this.data.image_code,
        type: 'register',
        mobile: this.data.mobile,
      })
        .then(() => {
          this.time = 60 * 1000;
          this.$notify({
            background: '#EAF1FF',
            color: '#3292FF',
            message: this.$t('lang.send_success'),
          });
          this.getImageCode();
        })
        .catch(() => {
          this.is_send = false;
          this.getImageCode();
        });
    },
    judgeApp(){
    	var userAgent = navigator.userAgent;
    	var type='Android';
    	var str = this.$t('lang.download_note1');
    	if (userAgent.indexOf("iPhone") > -1 || userAgent.indexOf("Mac") > -1) {
    	    type='ios'
    		str +=this.$t('lang.download_note2');
    	}else{
    		str +=this.$t('lang.download_note3');
    	}
    	str += this.$t('lang.download_note4');
    	
    	Dialog.confirm({
    		message: str,
    		showCancelButton:false,
    		confirmButtonText:this.$t('lang.download_btn2'),
    	})
    	.then(() => {// on confirm
    		var link =  this.config.app_android;
    		if(type=='ios'){
    			var arr = this.config.app_ios.split(',')
    			if(!arr.length){
    				return;
    			}
    			location.href = arr[0];
    			setTimeout(function(){
    				location.href = arr[1];
    			},2500)
    		}else{
    			if(link){
    				location.href = link;
    			}
    		}
    	})
    	.catch(() => {
    		this.$router.replace('/index');
    	});
    },
	handleSubmit() {
      if (!this.data.mobile) {
        this.$notify({
          message: this.$t('lang.input_mobile'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.password) {
        this.$notify({
          message: this.$t('lang.input_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (this.data.password !== this.data.spassword) {
        this.$notify({
          message: this.$t('lang.not_match_pw'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.data.code) {
        this.$notify({
          message: this.$t('lang.input_varify_code'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      if (!this.checked) {
        this.$notify({
          message: this.$t('lang.need_agree'),
          color: '#FF3232',
          background: '#FFEAEA',
        });
        return;
      }

      Fetch('/api/api/register', {
        ...this.data,
      }).then(res => {
        this.$notify({
          background: '#EAF1FF',
          color: '#3292FF',
          message: this.$t('lang.register_success'),
        });

        //this.$router.replace('/user');
		
		if(this.data.t_mobile){
			this.judgeApp();
			return;
		}
		
		this.$router.replace('/login');
		
        // var orgHost = ['muyunkj.com', 'www.muyunkj.com'];
        // var orgHost = res.data ? res.data.promotion_host : [];
        // var wpHost = document.location.hostname;
        // console.log(wpHost);
        // console.log(orgHost);
        // if (res.data.app_link && orgHost.indexOf(wpHost) != -1) {
        //   // 下载链接
        //   location.href = res.data.app_link;
        // } else {
        //   this.$router.replace('/user');
        // }
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import './css/login.less';

.top_bg:lang(yn) {
  p {
    line-height: 1rem;
    font-size: 0.6rem !important;
  }
}

.newbg:lang(yn) {
  .form.register {
    .content {
      p {}

      input:-ms-input-placeholder {
        font-size: 0.2375rem;
      }

      /* Internet Explorer 10+ */

      input::-webkit-input-placeholder {
        font-size: 0.2375rem;
      }

      /* WebKit browsers */

      input::-moz-placeholder {
        font-size: 0.2375rem;
      }

      /* Mozilla Firefox 4 to 18 */

      input:-moz-placeholder {
        font-size: 0.2375rem;
      }

      /* Mozilla Firefox 19+ */
    }
  }
}

.van-count-down:lang(yn) {
  span {
    font-size: 0.1rem !important;
  }
}
</style>
